@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@900&display=swap');

body {
  background-color: black;
}

.splide__arrow {
  z-index: 999;
}

.mainDiv {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  height: 100vh;
  width: 100vw;
}

.Tdiv {
  position: fixed;
}

.tv {
  position: absolute;
  right : 25vw;
  transition: 1s;
  z-index: 99;
  pointer-events: none;
}


.titre {
  color: white;
  position: absolute;
  text-transform: uppercase;
  font-size: 100px;
  letter-spacing: 15px
}

.background-text {
  display: none;
  position: absolute;
  top: 40vh;
  padding-left: 190vw;
  font-size: 20vh;
  font-family: "Orbitron";
  text-transform: uppercase;
  white-space: nowrap;
  overflow: hidden;
  color: #00000080;
  -webkit-animation: stream-title 10s infinite linear;
  animation: stream-title 10s infinite linear;
  z-index: -1;
}

.background-text-delay {
  -webkit-animation-delay: 5s;
  animation-delay: 5s;
}


@keyframes stream-title {
  0% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(-100%);
  }
}

@media only screen and (min-width: 768px) and (max-width: 1024px) {
  .background-text {
      padding-left: 380vw;
  }
}

@media only screen and (max-width: 767px) {
  .background-text {
      padding-left: 450vw;
  }
}

main .welcome {
  display: grid;
  grid-auto-flow: column;
  margin-top: 50vh;
}

.btn--big {
  padding: 10px 20px;
  border: 0;
  background: #f6edd7;
  -webkit-box-shadow: 0 0 10px rgba(246, 237, 215, 0.6);
  box-shadow: 0 0 10px rgba(246, 237, 215, 0.6);
  text-transform: uppercase;
  font-family: "Orbitron";
  font-size: 14px;
  font-size: 14rem;
  color: #0d0d0d;
  text-decoration: none; 
  font-size: 1vw;
  transition: 0.5s;
  cursor: pointer;
}

.btn--big:hover {
  background: #f18f0f;
  -webkit-box-shadow: 0 0 10px rgba(247, 158, 24, 0.6);
  box-shadow: 0 0 10px rgba(247, 158, 24, 0.6);
}

main {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 100;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;

}

.left, .right {
  text-align: center;
}

.center {
  width: 40vw;
}

@media screen and (min-width: 320px) {
  .btn--big {
      font-size: calc(9px + 11*((100vw - 320px) / 1080));
  }
}

@media only screen and (max-width: 1024px) and (min-width: 320px) {
  main .welcome .btn {
      font-size: calc(16px + 4*((100vw - 320px) / 1080));
  }
}

@media only screen and (max-width: 1024px) {
  main .welcome {
      grid-auto-flow: row;
      grid-template-rows: 50px auto;
      margin-top: 70vh;
  }

  .center{
    display: none;
  }
}

.Tdiv{
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  height: 100vh;
  width: 100vw;
}

.gallery__container {
  width: 100vw;
  /* height: 100vh; */
}

.slide {
  display: flex;
  width: 100vw;
  height: 100vh;
  align-items: center;
  /* justify-content: center; */
}

.un {
  background-color: #219b21;
}

.deux {
  background-color: #f18f0f;
}

.trois {
  background-color: #5151ff;
}

.quatre {
  background-color: #800080;
}

.slide div {
  position: absolute;
  margin-left: 50vw;
}

.slide h1 {
  /* right: 25vw; */
  font-size: 8vh;
  /* top: calc(50vh - 1em); */
  color: #f6edd7;
  text-shadow: 0 0 10px #f6edd7;
  text-transform: uppercase;
  font-family: "Orbitron";
}

.slide p {
  /* text-shadow: 0 0 10px #f6edd7;
  color: #f6edd7; */
  font-family: "Orbitron";
  margin-right: 1em;

}


@media only screen and (max-width: 945px) {
  .tv {
    right: 100vw;
    display: none;
  }
}

@media only screen and (max-width: 945px) {
  .gallery__container {
    width: 80vw;
    height: 80vh;
  }

  .slide div {
    right: 0;
    margin-left: 0;
    left: 0;
    text-align: center;
  }

  .slide p {
    display: none;
  
  }
  
  .slide{
  justify-content: center;
  display: flex;
  width: 80vw;
  height: 80vh;
  align-items: center;
  /* justify-content: center; */
  }

  .slide {
    background-image: url("https://thispersondoesnotexist.com/");
    background-size: 100vh;
    background-blend-mode: soft-light;
  }

}