* {
    box-sizing: border-box;
  }
  
  .info {
    width: 50vw;
    height: 95vh;
    background-color: white;
    position: absolute;
    z-index: 9999;
    left: 200vw;
    border-radius: 20px;
    transition: 1s;
    padding: 2em;
  }
  
  .container {
    display: flex;
    background-color: hsla(34, 89%, 50%, 0);
    width: 100vw;
    height: 100vh;
    position: absolute;
    z-index: 1;
    align-items: center;
    transition: 1s;
    pointer-events: none;
  }
  
  .active {
    display: flex;
    width: 100vw;
    height: 100vh;
    position: absolute;
    align-items: center;
    transition: 1s;
    z-index: 9998;
    pointer-events: all;
  }
  .right {
    left: 47vw;
  }
  
  @media only screen and (max-width: 945px) {
    .info {
      width: 100vw;
      height: 95vh;
    }
    .right {
      left: 0;
    }
  }
  
  .close {
    width: 60px;
    height: 60px;
    position: relative;
    background: #455A64;
    border-radius: 50%;
    box-shadow: 0 0 20px -5px rgba(255, 255, 255, 0.5);
    transition: 0.25s ease-in-out;
    cursor: pointer;
    -webkit-animation: fade-in 0.5s ease-out 0.25s both;
    animation: fade-in 0.5s ease-out 0.25s both;
}

@-webkit-keyframes fade-in {
    from {
        opacity: 0;
        transform: scale(1.1);
    }
}

@keyframes fade-in {
    from {
        opacity: 0;
        transform: scale(1.1);
    }
}

.close .circle path {
    stroke: white;
    fill: none;
    stroke-width: 1px;
    stroke-linecap: round;
    -webkit-animation: progress 0.5s ease-out 0.25s both;
    animation: progress 0.5s ease-out 0.25s both;
}

@-webkit-keyframes progress {
    from {
        stroke-dasharray: 0 100;
    }
}

@keyframes progress {
    from {
        stroke-dasharray: 0 100;
    }
}

.close span {
    display: block;
    width: 15px;
    height: 2px;
    background: white;
    box-shadow: 0 0 20px -5px rgba(255, 255, 255, 0.5);
    border-radius: 20px;
    position: absolute;
    transition: 0.25s ease-in-out;
    -webkit-animation: slide-in 0.5s ease-in-out 0.25s both;
    animation: slide-in 0.5s ease-in-out 0.25s both;
}

@-webkit-keyframes slide-in {
    from {
        width: 0;
    }
}

@keyframes slide-in {
    from {
        width: 0;
    }
}

.close span:nth-child(1) {
    top: 19px;
    left: 20px;
    transform: rotate(45deg);
    transform-origin: top left;
}

.close span:nth-child(2) {
    top: 19px;
    right: 20px;
    transform: rotate(-45deg);
    transform-origin: top right;
}

.close span:nth-child(3) {
    bottom: 19px;
    left: 20px;
    transform: rotate(-45deg);
    transform-origin: bottom left;
}

.close span:nth-child(4) {
    bottom: 19px;
    right: 20px;
    transform: rotate(45deg);
    transform-origin: bottom right;
}

.close:hover {
    background: #757575;
}

/* Responsive CSS */
@media (max-width: 768px) {
    .close {
      transform: scale(1.1);
    }
}

@media (max-width: 480px) {
  .close {
    transform: scale(0.7);
  }
}


  
  
